h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --liner-grey: #121212linear-gradient( 180deg, rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16% );
  --orange: #f87719;
  --liner-orange: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --liner-orange-light: linear-gradient( 141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91% );
  --grey-white-theme: #595959;
  --secondary-black: #282828;
  --background: #111;
  --secondary-text: #f8f8f8;
  --grey: #b7b7b7;
  --white: #fff;
}

body {
  font-family: Roboto, sans-serif;
}

.section {
  padding-top: 40px;
  padding-bottom: 40px;
}

@media screen and (min-width: 768px) {
  .section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.container {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 480px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

.button-text {
  color: var(--background);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
}

@media screen and (min-width: 768px) {
  .button-text {
    font-size: 14px;
    line-height: 1.4;
  }
}

@media screen and (min-width: 1280px) {
  .button-text {
    font-size: 16px;
